:root {
    --primary: #4628ff;
    --warning: #ffd028;
    --danger: #eb3f27;
    --success: #75fa83;
    --white: #fdfdfd;
    --dark: #181717;
}

.btn{
   /*width: 40vw;
    height:10vh; */
    font-family: "Calibri", sans-serif;
    font-weight:  400;
    border-radius: 10px;
    cursor: pointer;
    transition: transform 0.3 ease;
}

.btn:hover{
    transform: translateY(-3px)
}

/* Button Colors and Styles */

.btn--primary--solid{
 background-color: var(--primary);
 color: var(--white);
 border: none;
}

.btn--warning--solid{
    background-color: var(--warning);
    color: var(--dark);
    border: none;
}

.btn--danger--solid{
    background-color: var(--danger);
    color: var(--white);
    border: none;
}

.btn--success--solid{
    background-color: var(--success);
    color: var(--white);
    border: none;
}

.btn--primary--outline{
    background-color: transparent;
    color: var(--primary);
    border: 2px solid var(--primary);
}

.btn--warning--outline{
    background-color: transparent;
    color: var(--warning);
    border: 2px solid var(--warning);
}

.btn--danger--outline{
    background-color: transparent;
    color: var(--danger);
    border: 2px solid var(--danger);
}

.btn--success--outline{
    background-color: transparent;
    color: var(--success);
    border: 2px solid var(--success);
}

/* Button Sizes */

.btn--medium{
    padding: 10px 20px;
    font-size: 150%;   
}

.btn--large{
    padding: 15px 30px;
    font-size: 300%;   
}